@import url(https://fonts.googleapis.com/css2?family=Odibee+Sans&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Odibee Sans', cursive;
}
body{
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  background-color: rgb(33, 56, 56);
  box-shadow: 0 0 175px rgba(0,0,0,0.2) inset;
}
.App {
  margin: auto;
  width: 80%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}
.pads-container {
  margin: 20px auto;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  max-width: 730px;
  width: 90%;
}

.heading {
    margin: 25px auto 10px auto;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-wrap: wrap-reverse;
            flex-wrap: wrap-reverse;
    font-size: 40px;
    text-align: center;
    color: white;
    text-shadow: 6px 8px 8px rgba(0, 0, 0, 0.301);
}
.pad {
  position: relative;
  border: 1px solid black;
  min-width: 115px;
  width: 33.333%;
  padding-top: 33.3333%;
  -webkit-box-shadow: 0px 0px 51px -3px rgba(0, 0, 0, 0.52) inset;
  -moz-shadow: 0px 0px 51px -3px rgba(0, 0, 0, 0.52) inset;
  box-shadow: 0px 0px 51px -3px rgba(0, 0, 0, 0.52) inset;
}

.pad-content {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.pad-not-active{
  background-color: darkslategrey;
}
.pad-color-0 {
  background-color: #3150F6;
}
.pad-color-1 {
  background-color: #2A71D4;
}
.pad-color-2 {
  background-color: #3BB4EA;
}
.pad-color-3 {
  background-color: #2ACBD4;
}
.pad-color-4 {
  background-color: #31F6CA;
}

.play-pause-btn > p{
    font-size: 50px;
    color: white;
}
